import * as React from "react";
import { styled } from "@mui/material/styles";
import Box from "@mui/material/Box";
import Paper from "@mui/material/Paper";
import Grid from "@mui/material/Grid";
import { Button, Typography } from "@mui/material";
import { Container } from "@mui/system";
import homeImage from "../images/landingPage.png";
import secondImage from "../images/secondPage.png";
import "../components/Home.css";
const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === "dark" ? "#1A2027" : "#fff",
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: "center",
  color: theme.palette.text.secondary,
}));

export default function BasicGrid() {
  return (
    <Container>
      <Box sx={{ flexGrow: 1 }}>
        <Grid
          container
          spacing={4}
          sx={{
            marginTop: "120px",
          }}
        >
          <Grid item xs={12} lg={6}>
            <Typography
              className="homeText"
              sx={{
                fontFamily: "Poppins",
                fontStyle: "Normal",
                fontWeight: 700,
                fontSize: { lg: "48px", xs: "34px" },
                lineHeight: "60px",
                color: "#1F2E35",
                textAlign: { lg: "start", xs: "center" },
              }}
            >
              Making the most of the ever-growing
            </Typography>
            <Typography
              className="hometred"
              sx={{
                fontFamily: "Poppins",
                fontStyle: "Normal",
                fontWeight: 700,
                fontSize: { lg: "45px", xs: "34px" },
                lineHeight: "133.69%",
                color: "#ee4248",
                textAlign: { lg: "start", xs: "center" },
              }}
            >
              Information Technology
            </Typography>
            <Typography
              sx={{
                textAlign: { lg: "start", xs: "center" },
                fontFamily: "Poppins",
                fontStyle: "Normal",
                fontWeight: 700,
                fontSize: { lg: "20px", xs: "18px" },
              }}
            >
              Managed by a team of professional experts with extensive
              experience and impressive track records
            </Typography>
            <Button
              variant="contained"
              sx={{
                fontFamily: "Poppins",
                background: "#ee4248",
                borderRadius: "4px",
                color: "#ffffff",
                padding: "12px 32px",
                marginRight: "410px",
                width: "161px",
                height: "53px",
                marginTop: 3,
                marginLeft: { xs: "155px", lg: "0px " },
                marginBottom: 10,
                boxShadow: "1px 2px 4px rgba(0, 0, 0, 0.25)",
                align: { lg: "start", xs: "center" },
              }}
            >
              Read more
            </Button>
          </Grid>
          <Grid item xs={12} md={6} align="center">
            <Box>
              <img
                src={homeImage}
                alt="Home-page"
                className="homeImage"
                sx={{
                  height: { lg: "390px", xs: "auto" },
                  width: { xs: "fixed" },
                  background: "#F7F7F7",
                }}
              />
            </Box>
          </Grid>
          <Grid item xs={8} md={6} align="start">
            <Box>
              <img
                src={secondImage}
                alt="Home-page"
                className="homeImage"
                sx={{
                  height: { lg: "390px", xs: "auto" },
                  width: { xs: "fixed" },
                }}
              />
            </Box>
          </Grid>
          <Grid item xs={12} md={6}>
            <Typography
              sx={{
                fontFamily: "Poppins",
                fontSize: { lg: "35px", xs: "28px" },
                marginTop: "55px",
                textAlign:"center"
              }}
            >
              Welcome to{" "}
              <span id="anotherOne">Dwidasa Samsara Indonesia (DSI)</span>
            </Typography>
            <Grid>
              <Typography
                sx={{
                  fontFamily: "Poppins",
                  fontSize: { lg: "20px", xs: "18px" },
                  marginTop: "25px",
                  textAlign: "center",
                }}
              >
                Managed by a team of professional experts with extensive
                experience and impressive track records, DSI believes that
                continuous improvements and innovations assure your business to
                run effectively and efficiently. <br />
                <br />
                Dwidasa Samsara Indonesia (DSI) was firstly established in 2010
                by the founders, who each of them has a common end objective to
                innovate new creations by making the most of the ever-growing
                information technology through DSIs distinct front-end based
                application concept.
              </Typography>
            </Grid>
          </Grid>
        </Grid>
      </Box>
    </Container>
  );
}
